import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from  'axios';
import { Text } from 'gestalt';
import { Masonry } from 'gestalt';
import { Image } from 'gestalt';
import { Card } from 'gestalt';
import { Box } from 'gestalt';
import { Avatar } from 'gestalt';
import { Link } from 'gestalt';
import { Icon } from 'gestalt';
import { Spinner } from 'gestalt';
import { Button } from 'gestalt';

// box color list
const boxColor = ["blue","darkGray","darkWash","eggplant","gray","green","lightGray","lightWash","maroon","midnight","navy","olive","orange","orchid","pine","purple","red","transparent","watermelon","white"];
// 当前滚动位置
const  getScrollPosition = ()=>{
    let t, l, w, h;
    if (document.documentElement && document.documentElement.scrollTop) {
        t = document.documentElement.scrollTop;
        l = document.documentElement.scrollLeft;
        w = document.documentElement.scrollWidth;
        h = document.documentElement.scrollHeight;
    } else if (document.body) {
        t = document.body.scrollTop;
        l = document.body.scrollLeft;
        w = document.body.scrollWidth;
        h = document.body.scrollHeight;
    }
    return {
        top: t,
        left: l,
        width: w,
        height: h
    };
};
class Item extends Component {
    constructor(props) {
        super(props);
        this.state = {
            item: props.data,
            itemIdx: props.itemIdx,
            boxColor: boxColor,
            style: {
                clear:'both',
                overflow:'hidden',
                width:'100%',
            },
            hovered: false
        };
    }

    // 在第一次渲染后调用，只在客户端。之后组件已经生成了对应的DOM结构，可以通过this.getDOMNode()来进行访问。
    componentDidMount() {

    }

    // 在组件从 DOM 中移除的时候立刻被调用。
    componentWillUnmount() {

    }
    render() {
        let imgStyle = {
            'border-radius': '5px'
        };
        return (
            <div style={this.state.style} className={'pinItem'}>
                <Box paddingX={2} paddingY={3} shape={'square'}>
                    <Card
                        paddingX={3}
                        paddingY={2}
                        onMouseEnter={()=>{
                            this.setState({
                                hovered: true
                            })
                        }}
                        onMouseLeave={()=>{
                            this.setState({ hovered: false });
                        }}>
                        <Box shape={'roundedTop'} color={this.state.boxColor[this.state.itemIdx]}>
                            <Image
                                alt={this.state.item.text}
                                // fit="cover"
                                color = "rgb(111,91,77)"
                                naturalWidth={this.state.item.pic_detail  ? this.state.item.pic_detail.geo.width:360   }
                                naturalHeight={this.state.item.pic_detail  ? this.state.item.pic_detail.geo.height : 540}
                                src={this.state.item.pic_detail ? this.state.item.pic_detail.url :this.state.item.display_url}
                                style={imgStyle}>
                                <Box paddingX={3} paddingY={1} position={'absolute'} bottom={true} left={true} shape={'rounded'} color={'white'} marginLeft={3} marginBottom={3} display={this.state.hovered ? 'block' : 'none'}>
                                    <Link href={'weibo.com/u/'+this.state.item.wb_id}>
                                        <Box alignItems="center" display="flex">
                                            <Box marginRight={1} padding={1}>
                                                <Icon icon="arrow-up-right" accessibilityLabel="link" color="darkGray" inline={true}/>
                                            </Box>
                                            <Text align="center" bold color="darkGray">
                                                weibo.com
                                            </Text>
                                        </Box>
                                    </Link>
                                </Box>
                            </Image>
                        </Box>
                        <Box display="flex" direction="row" paddingY={2}>
                            <Box column={2} paddingY={2}>
                                <Link href={'weibo.com/u/'+this.state.item.wb_id} target={'blank'}>
                                    <Avatar name={this.state.item.screen_name} src={this.state.item.avatar} verified={this.state.item.verified} />
                                </Link>
                            </Box>
                            <Box column={10} padding={2}>
                                <Text color={'darkGray'} align={'left'} truncate>{this.state.item.description}</Text>
                                <Text color={'gray'} align={'left'} truncate>
                                    <Link href={'weibo.com/u/'+this.state.item.wb_id} target={'blank'}>{this.state.item.screen_name}</Link>
                                </Text>
                            </Box>
                        </Box>
                    </Card>
                </Box>
            </div>
        );
    }
}
class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // pins: items,
            is_load:true,
            count: 16,
            total: 0,
            pins: []
        };
    }
    // 获取初始数据
    getData(){
        let that = this;
        axios.get('/getImages').then((res)=>{
            // console.log(res);
            that.setState({
                total: res.data.total,
                pins: res.data.data,
                is_load: res.data.next_page_url,
                last_page: res.data.last_page,
                current_page: res.data.current_page,
                show_spinner: false
            });
        }).catch((error)=>{
            console.log(error);
        });
    }
    // 在第一次渲染后调用，只在客户端。
    // 你应该在 componentDidMount 生命周期方法内发送 AJAX 请求数据。
    // 这样你才能够在请求的数据到达时使用 setState 更新你的组件。
    componentDidMount() {

    }
    componentWillMount() {
        this.getData();
    }
    componentWillUnmount() {

    }
    render() {
        return (
            <div className="App">
                <Box color="white" shape="rounded" padding={3}>
                    <Link href="/">
                        <Box padding={2}>
                            <Text bold>starImg</Text>
                        </Box>
                    </Link>
                </Box>
                <Masonry
                    comp={Item}
                    items={this.state.pins}
                    loadItems={(event)=>{
                        // setTimeout(()=>{
                        //     if(this.state.is_load){
                        //         console.log('--load --more--');
                        //         let data = images.data;
                        //         this.setState({
                        //             pins: this.state.pins.concat(data.splice(0,this.state.count)),
                        //             total: data.length,
                        //             is_load: this.state.total>0,
                        //             show_spinner: this.state.total>0
                        //         });
                        //     }else{
                        //         console.log('---nothing to load ---- ');
                        //         this.setState({
                        //             show_spinner: false
                        //         })
                        //     }
                        // },1000);
                    }}
                    minCols={2}
                    gutterWidth = {5}
                    flexible = {true}
                    // virtualize={true}
                    // virtualBoundsBottom={50}
                    // scrollContainer = {()=> document.querySelector('body')}
                    scrollContainer = {()=> document.getElementById('app')}
                />
                <Spinner accessibilityLabel={'Load more Pins'} show={this.state.show_spinner}/>
                <Box margin={2} display={this.state.is_load ? 'block': 'none'}>
                    <Box padding={2}>
                        <Button text="load more images" onClick={(event)=>{
                            let that = this;
                            that.setState({
                                show_spinner: true
                            });
                            let page = that.state.current_page+1;
                            console.log(page,that.state.last_page);
                            if(page<that.state.last_page){
                                axios.get('/getImages?page='+page).then((res)=>{
                                    that.setState({
                                        total: res.data.total,
                                        pins: that.state.pins.concat(res.data.data),
                                        is_load: res.data.next_page_url,
                                        last_page: res.data.last_page,
                                        current_page: res.data.current_page,
                                        show_spinner: false
                                    });
                                    console.log(res);
                                }).catch((error)=>{
                                    console.log(error);
                                });
                                console.log('--load --more--');
                            }else{
                                console.log('---nothing to load ---- ');
                                this.setState({
                                    show_spinner: false
                                })
                            }
                        }}/>
                    </Box>
                </Box>
            </div>
        );
    }
}

ReactDOM.render(<App />, document.getElementById('app'));



